@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
.main-page .main-hero {
  padding-top: 150px;
  padding-bottom: 20px;
  background: #51bf87;
  background: -webkit-linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
  background: -o-linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
  background: -moz-linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
  background: linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
  transition: padding .25s;
}

.main-page .main-hero .header {
  margin: 0px;
  text-align: center;
  color: #ecc969;
  text-shadow: 0px 0px 19px #3d7a68;
  letter-spacing: 7px;
  font-size: 43px;
  font-weight: 400;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  margin-bottom: 50px;
  transition: all .25s;
}

.main-page .main-hero .hero-img {
  margin: 0 auto;
  max-height: 350px;
  transition: max-height .25s;
}

/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) and (min-width: 993px) {
  .main-page .main-hero {
    padding-top: 140px;
  }
  .main-page .main-hero .header {
    letter-spacing: 6px;
    font-size: 40px;
    margin-bottom: 30px;
  }
  .main-page .main-hero .hero-img {
    max-height: 330px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) and (min-width: 769px) {
  .main-page .main-hero {
    padding-top: 130px;
  }
  .main-page .main-hero .header {
    letter-spacing: 6px;
    font-size: 37px;
    margin-bottom: 30px;
  }
  .main-page .main-hero .hero-img {
    margin: 0 auto;
    max-height: 310px;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width: 768px) and (min-width: 481px) {
  .main-page .main-hero {
    padding-top: 120px;
  }
  .main-page .main-hero .header {
    letter-spacing: 5px;
    font-size: 35px;
    margin-bottom: 30px;
  }
  .main-page .main-hero .hero-img {
    max-height: 290px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) and (min-width: 321px) {
  .main-page .main-hero {
    padding-top: 110px;
  }
  .main-page .main-hero .header {
    letter-spacing: 3px;
    font-size: 31px;
    margin-bottom: 30px;
  }
}

/* Custom, iPhone Retina */
@media only screen and (max-width: 320px) {
  .main-page .main-hero {
    padding-top: 100px;
  }
  .main-page .main-hero .header {
    letter-spacing: 2px;
    font-size: 28px;
    margin-bottom: 20px;
  }
}
