@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
.main-navbar-section .navbar {
  background-color: transparent;
  border-bottom: 1px solid transparent;
  transition: all .25s;
}

.main-navbar-section .navbar .content {
  height: 100px;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -ms-flex-align: center;
  -webkit-align-items: center;
  -webkit-box-align: center;
  align-items: center;
  transition: height .5s;
}

.main-navbar-section .navbar .content .logo-container .logo {
  transition: font-size .25s;
  color: #fff;
  text-decoration: none;
  font-size: 29px;
  font-weight: 300;
}

.main-navbar-section .navbar .content .logo-container .logo .logo-initials {
  transition: font-size .25s;
  color: #ecc969;
  font-size: 31px;
}

.main-navbar-section .navbar .content .contact-link-container .contact-link {
  position: relative;
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  font-weight: 300;
  border: 2px solid #fff;
  border-radius: 3px;
  padding: 7px 20px;
  transition: all .25s;
}

.main-navbar-section .navbar .content .contact-link-container .contact-link:hover {
  background-color: #fff;
  color: #51bf87;
}

.main-navbar-section .first-scroll-navbar {
  background-color: rgba(81, 191, 135, 0.7) !important;
}

.main-navbar-section .first-scroll-navbar .content {
  height: 70px !important;
}

.main-navbar-section .second-scroll-navbar {
  background-color: #51bf87 !important;
  border-bottom-color: #3eb177;
}

@media (max-width: 767px) {
  .main-navbar-section .navbar .content {
    height: 70px;
    padding: 0px !important;
  }
  .main-navbar-section .navbar .content .logo-container .logo {
    font-size: 23px;
  }
  .main-navbar-section .navbar .content .logo-container .logo .logo-initials {
    font-size: 25px;
  }
  .main-navbar-section .navbar .content .contact-link-container .contact-link {
    font-size: 16px;
    padding: 5px 20px;
  }
  padding: 0px !important;
}
